strumento de control o castigo, imponiendo autoridad de manera autoritaria. En el aula, esto se traduce en el uso de las calificaciones como sanciones. Además, los docentes se ven obligados a cumplir con normas estrictas que no siempre son adaptables. En el contexto del SENA, la evaluación debe ser equitativa y justa, evitando su uso como herramienta coercitiva y enfocándose en el desarrollo formativo de los aprendices.")])])])]),_vm._m(30),_vm._m(31),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-sm-12 col-lg-8"},[_c('LineaTiempoD',{staticClass:"color-acento-contenido"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo":"Autoevaluación"}},[_c('div',{staticClass:"col-sm-12 col-lg-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es un proceso reflexivo en el que el estudiante, de manera autónoma, evalúa su propio desempeño utilizando criterios específicos. Representa el máximo ejercicio de autorregulación y responsabilidad en el proceso evaluativo, donde el aprendiz puede definir tanto los criterios como los juicios sobre su trabajo. En el contexto del SENA, esta práctica fomenta la capacidad de los estudiantes para valorar su progreso, lo cual es esencial para su desarrollo profesional y personal.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Coevaluación"}},[_c('div',{staticClass:"col-sm-12 col-lg-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es un proceso en el que pares o colegas se evalúan mutuamente, basado en el compromiso y el reconocimiento de una situación común. Este tipo de evaluación es frecuente entre grupos de estudiantes o docentes investigadores que valoran el trabajo de sus iguales. Para el SENA, la coevaluación es una herramienta colaborativa que promueve la construcción de un marco de referencia compartido y la mejora conjunta de procesos formativos.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"3","titulo":"Heteroevaluación"}},[_c('div',{staticClass:"col-sm-12 col-lg-12"},[_c('p',{sta